.orderSuccess {
  text-align: center;
  padding: 10px;
  height: 70vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.orderSuccess > svg {
  font-size: 15vh;
  color: rgb(0, 102, 109);
}

.orderSuccess > p {
  font-size: 20px;
}

.orderSuccess > a {
  background-color: rgb(51, 51, 51);
  color: white;
  border: none;
  padding: 1vmax 3vmax;
  cursor: pointer;
  font: 400 18px "Roboto";
  text-decoration: none;
  margin: 2vmax;
}

@media screen and (max-width: 600px) {
  .orderSuccess > a {
    padding: 3vw 6vw;
    font: 400 4vw "Roboto";
    margin: 2vmax;
  }

  .orderSuccess > svg {
    font-size: 20vw;
  }

  .orderSuccess > p {
    margin: 2vmax;
    font-size: 5vw;
  }
}
