.confirmOrderPage {
  height: 90vh;
  display: grid;
  grid-template-columns: 6fr 3fr;
}

.confirmOrderPage > div:last-child {
  border-left: 1px solid rgba(0, 0, 0, 0.247);
}

.confirmshippingArea {
  padding: 5vmax;
  padding-bottom: 0%;
}

.confirmshippingArea > p {
  font: 400 16px "Roboto";
}

.confirmshippingAreaBox,
.confirmCartItemsContainer {
  margin: 2vmax;
}

.confirmshippingAreaBox > div {
  display: flex;
}

.confirmshippingAreaBox > div > p {
  font: 400 12px "Roboto";
  color: black;
}

.confirmshippingAreaBox > div > span {
  margin: 12px 1vmax;
  font: 100 12px "Roboto";
  color: #575757;
}

.confirmCartItems > p {
  font: 400 16px "Roboto";
}

.confirmCartItems {
  padding: 5vmax;
  padding-top: 2vmax;
}

.confirmCartItemsContainer {
  max-height: 20vmax;
  overflow-y: auto;
}

.confirmCartItemsContainer::-webkit-scrollbar {
  width: 5px;
  background-color: #cbcdcb;
}

.confirmCartItemsContainer::-webkit-scrollbar-thumb {
  width: 5px;
  background-color: #1b4da4f1;
  border-radius: 1rem;
}

.confirmCartItemsContainer > div {
  display: grid;
  grid-template-columns: 1fr 2fr 2fr;
  font: 400 12px "Roboto";
  align-items: center;
  justify-content: space-between;
  margin: 2vmax 0;
}

.confirmCartItemsContainer > div > img {
  width: 3vmax;
}

.confirmCartItemsContainer > div > a {
  color: #575757;
  margin: 0 2vmax;
  width: 60%;
  text-decoration: none;
}

.confirmCartItemsContainer > div > span {
  font: 100 12px "Roboto";
  color: #5e5e5e;
}

.orderSummary {
  padding: 7vmax;
}

.orderSummary > p {
  text-align: center;
  font: 400 1.8vmax "Roboto";
  border-bottom: 1px solid rgba(0, 0, 0, 0.267);
  padding: 1vmax;
  width: 100%;
  margin: auto;
  box-sizing: border-box;
}

.orderSummary > div > div {
  display: grid;
  grid-template-columns: 1fr 2fr;
  font: 300 12px "Roboto";
  justify-content: space-between;
  margin: 2vmax 0;
}

.orderSummary > div > div > span {
  color: rgba(0, 0, 0, 0.692);
  margin: 0px;
  margin-top: 10px;
  text-align: right;
  padding-right: 14px;
}

.orderSummaryTotal {
  display: flex;
  font: 300 12px "Roboto";
  justify-content: space-between;
  border-top: 1px solid rgba(0, 0, 0, 0.363);
  padding: 2vmax 0;
}

.orderSummary > button {
  background-color: rgb(0, 107, 119);
  color: white;
  width: 100%;
  padding: 1vmax;
  border: none;
  margin: auto;
  cursor: pointer;
  transition: 0.5s;
  font: 400 12px "Roboto";
}

.orderSummary > button:hover {
  background-color: rgb(0, 70, 108);
}

@media screen and (max-width: 600px) {
  .confirmOrderPage {
    grid-template-columns: 1fr;
    height: unset;
  }

  .confirmOrderPage > div:last-child {
    border-left: 0;
    border-top: 1px solid rgba(0, 0, 0, 0.247);
  }

  .confirmshippingArea {
    padding: 2vmax;
  }

  .confirmshippingArea > p {
    font: 400 16px "Roboto";
  }

  .confirmshippingAreaBox > div {
    display: flex;
    margin: 1vh 0;
  }

  .confirmshippingAreaBox > div > p {
    font: 400 12px "Roboto";
  }

  .confirmshippingAreaBox > div > span {
    font: 100 12px "Roboto";
  }

  .confirmCartItems > p {
    font: 400 16px "Roboto";
  }

  .confirmCartItems {
    padding: 1vmax;
    margin: 10px;
    padding-top: 2vmax;
  }

  .confirmCartItemsContainer {
    max-height: 50vw;
    width: 100vw;
    margin-left: 0vw;
  }

  .confirmCartItemsContainer > div {
    font: 400 13px "Roboto";
    margin: 10px 0;
    margin-right: 0px;
  }

  .confirmCartItemsContainer > div > img {
    width: 10vw;
  }

  .confirmCartItemsContainer > div > a {
    margin: 0;
    width: 20%;
  }

  .confirmCartItemsContainer > div > span {
    font: 100 12px "Roboto";
  }

  .orderSummary {
    padding: 10px;
    margin: 10px;
    width: 90vw;
  }

  .orderSummary > p {
    font: 400 16px "Roboto";
    padding: 4vw;
  }

  .orderSummary > div > div {
    font: 300 12px "Roboto";
  }

  .orderSummaryTotal {
    font: 300 12px "Roboto";
    padding: 5vw 0;
    padding-right: 15px;
  }

  .orderSummary > button {
    padding: 4vw;
    margin: 4vw auto;
    font: 400 20px "Roboto";
    width: 85vw;
  }
}
