.paymentContainer {
  display: grid;
  place-items: center;
  height: 65vh;
  margin: 2vmax;
}

.paymentForm {
  background-color: rgb(255, 255, 255);
  width: 50vw;
  height: 60vh;
  padding: 20px;
  border: 1px solid rgb(199, 198, 198);
  border-radius: 10px;
}

.paymentForm > p {
  font: 400 20px "Roboto";
  color: rgba(0, 0, 0, 0.753);
  border-bottom: 1px solid rgba(0, 0, 0, 0.13);
  padding: 1vmax 0;
  text-align: center;
  width: 50%;
  margin: auto;
}

.paymentForm > div {
  display: flex;
  align-items: center;
  margin: 40px 0;
}

.paymentInput {
  padding: 15px;
  padding-left: 35px;
  width: 100%;
  box-sizing: border-box;
  border: 1px solid rgba(0, 0, 0, 0.267);
  border-radius: 4px;
  outline: none;
}

.paymentForm > div > svg {
  position: absolute;
  transform: translateX(1vmax);
  font-size: 20px;
  color: rgba(0, 0, 0, 0.623);
}

.paymentFormBtn {
  border: none;
  background-color: rgb(0, 100, 117);
  color: white;
  font: 300 16px "Roboto";
  width: 100%;
  padding: 10px;
  cursor: pointer;
  transition: all 0.5s;
  outline: none;
}

.paymentFormBtn:hover {
  background-color: rgb(0, 66, 117);
}

@media screen and (max-width: 600px) {
  .paymentForm {
    width: 75vw;
    height: 60vh;
  }

  .paymentForm > p {
    padding: 12px 0;
    width: 60%;
  }

  .paymentForm > div {
    margin: 20px 0;
  }

  .paymentInput {
    padding: 4vw 10vw;
  }

  .paymentForm > div > svg {
    font-size: 6vw;
  }

  .paymentFormBtn {
    font: 300 14px "Roboto";
    padding: 10px;
  }
}
