.shippingContainer {
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.shippingBox {
  background-color: white;
  width: 50vw;
  height: 80vh;
  box-sizing: border-box;
  overflow: hidden;
  border: 1px solid rgb(190, 188, 188);
  border-radius: 10px;
}

.shippingHeading {
  text-align: center;
  color: rgba(0, 0, 0, 0.664);
  font: 400 20px "Roboto";
  padding: 1.3vmax;
  border-bottom: 1px solid rgba(0, 0, 0, 0.205);
  width: 50%;
  margin: auto;
}

.shippingForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  padding: 2vmax;
  justify-content: space-evenly;
  height: 80%;
  transition: all 0.5s;
}

.shippingForm > div {
  display: flex;
  width: 100%;
  align-items: center;
}

.shippingForm > div > input,
.shippingForm > div > select {
  padding: 1vmax 4vmax;
  padding-right: 1vmax;
  width: 100%;
  box-sizing: border-box;
  border: 1px solid rgba(0, 0, 0, 0.267);
  border-radius: 4px;
  font: 300 14px cursive;
  outline: none;
}

.shippingForm > div > svg {
  position: absolute;
  transform: translateX(1vmax);
  font-size: 1.6vmax;
  color: rgba(0, 0, 0, 0.623);
}

.shippingBtn {
  border: none;
  background-color: rgb(0, 115, 123);
  color: white;
  font: 300 14px "Roboto";
  width: 100%;
  padding: 1vmax;
  cursor: pointer;
  transition: all 0.5s;
  outline: none;
  margin: 2vmax;
}

.shippingBtn:hover {
  background-color: rgb(2, 53, 116);
}

@media screen and (max-width: 600px) {
  .shippingBox {
    width: 100vw;
    height: 70vh;
    background-color: #f8fdff;
    border: none;
  }

  .shippingHeading {
    font: 400 20px "Roboto";
    padding: 5vw;
  }

  /* .shippingForm {
        padding: 1vw;
    } */

  .shippingForm > div > input,
  .shippingForm > div > select {
    padding: 12px 10vw;
    font: 300 12px cursive;
  }

  .shippingForm > div > svg {
    font-size: 6vw;
    transform: translateX(3vw);
  }

  .shippingBtn {
    font: 300 14px "Roboto";
    padding: 2vw;
  }
}
