.myOrdersPage {
  width: 95vw;
  padding: 0 7vmax;
  box-sizing: border-box;
  height: 90vh;
  display: flex;
  flex-direction: column;
}

#myOrdersHeading {
  text-align: center;
  font: 400 20px "Roboto";
  padding: 0.5vmax;
  box-sizing: border-box;
  color: rgb(255, 255, 255);
  transition: all 0.5s;
  background-color: rgb(44, 44, 44);
}

.myOrdersTable {
  background-color: white;
}

.myOrdersTable div {
  font: 300 16px "Roboto";
  border: none;
}

.myOrdersTable a {
  color: rgba(0, 0, 0, 0.527);
  transition: all 0.5s;
}

.myOrdersTable a:hover {
  color: rgb(0, 17, 115);
}

.MuiDataGrid-columnHeader {
  background-color: rgb(1, 113, 138);
  padding: 1vmax !important;
}

.MuiDataGrid-columnHeader div {
  color: rgb(255, 255, 255);
  font: 400 18px "Roboto" !important;
}

.MuiDataGrid-iconSeparator {
  display: none !important;
}

@media screen and (max-width: 600px) {
  .myOrdersPage {
    padding: 0;
    margin-left: 10px;
    height: 90vh;
    margin-right: 10px;
  }

  #myOrdersHeading {
    font: 400 12px "Roboto";
    padding: 4vw;
  }

  .myOrdersTable div {
    font: 300 12px "Roboto";
  }

  .MuiDataGrid-columnHeader {
    padding: 20px !important;
  }

  .MuiDataGrid-columnHeader div {
    font: 500 14px "Roboto" !important;
  }
}
