.searchBox {
  width: 100vw;
  height: auto;
  max-width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 15px;
}

.searchBox > input[type="text"] {
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.274);
  background-color: white;
  border: none;
  color: rgba(0, 0, 0, 0.637);
  padding: 1vmax 2vmax;
  width: 50%;
  outline: none;
  border-radius: 0%;
  font: 300 16px cursive;
  box-sizing: border-box;
  height: 8%;
}

.searchBox > input[type="submit"] {
  height: 8%;
  border-radius: 0%;
  background-color: rgb(1, 127, 116);
  border: none;
  padding: 1vmax 2vmax;
  width: 150px;
  font: 300 16px "Roboto";
  cursor: pointer;
  color: white;
  transition: all 0.5s;
}

.searchBox > input[type="submit"]:hover {
  background-color: rgb(231, 222, 97);
}

@media screen and (max-width: 600px) {
  .searchBox > input[type="text"] {
    width: 60%;
    font: 300 12px cursive;
    height: 10%;
  }

  .searchBox > input[type="submit"] {
    height: 10%;
    width: 20%;
    font: 300 12px "Roboto";
  }
}
